@import url(https://fonts.googleapis.com/css2?family=Goldman&family=Lobster&family=Montserrat:wght@400;600&family=Raleway&display=swap);
body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont,
        'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu',
        'Cantarell', 'Fira Sans', 'Droid Sans',
        'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas,
        'Courier New', monospace;
}

html,
#root,
body {
    height: 100%;
    width: 100%;
}

#root {
    z-index: 1;
}

/* Root Styles */
:root {
    --accent: hsla(4, 99%, 66%, 1);
    --accent-translucent: rgba(254, 93, 82, 0.7);
    --dark: hsla(214, 35%, 37%, 1);
    --dark-translucent: hsla(214, 35%, 37%, 0.25);
    --dark-translucent-1: hsla(214, 35%, 37%, 0.75);
    --dark-translucent-2: hsla(214, 35%, 37%, 0.5);
    --primary: hsla(210, 96%, 40%, 1);
    --primary-translucent: hsla(210, 96%, 40%, 0.7);
    --light: hsla(182, 82%, 93%, 1);
    --light-translucent: hsla(182, 82%, 93%, 0.7);
    --background: hsla(218, 23%, 21%, 1);
    --background-translucent: hsla(218, 23%, 21%, 0.85);
    --error: hsl(350, 96%, 43%);
    --error-translucent: hsla(350, 96%, 43%, 0.282);
    --heading-font: 'Goldman', cursive;
    --subheading-font: 'Raleway', sans-serif;
    --normal-font: 'Montserrat', sans-serif;
    --weird-font: 'Lobster', cursive;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Montserrat', sans-serif;
    font-family: var(--normal-font);
}

*::-webkit-scrollbar {
    width: 0.3rem;
    height: 0.3rem;
    cursor: pointer;
}

*::-webkit-scrollbar-track {
    background-color: hsla(4, 99%, 66%, 1);
    background-color: var(--accent);
}

*::-webkit-scrollbar-thumb {
    background-color: hsla(210, 96%, 40%, 1);
    background-color: var(--primary);
    border-radius: 100rem;
    cursor: pointer;
}

.curtains-canvas {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    z-index: -1;
}

a {
    text-decoration: none;
    color: hsla(182, 82%, 93%, 1);
    color: var(--light);
}

input:focus,
button:focus {
    outline: none;
}

/* COMMON CLASSES */
.background-accent {
    background-color: hsla(4, 99%, 66%, 1) !important;
    background-color: var(--accent) !important;
}

.background-accent-translucent {
    background-color: rgba(254, 93, 82, 0.7);
    background-color: var(--accent-translucent);
}

.background-light {
    background-color: hsla(182, 82%, 93%, 1);
    background-color: var(--light);
}

.background-light-translucent {
    background-color: hsla(182, 82%, 93%, 0.7);
    background-color: var(--light-translucent);
}

.background-dark {
    background-color: hsla(214, 35%, 37%, 1);
    background-color: var(--dark);
}

.background-dark-translucent {
    background-color: hsla(214, 35%, 37%, 0.25);
    background-color: var(--dark-translucent);
}

.background-primary {
    background-color: hsla(210, 96%, 40%, 1);
    background-color: var(--primary);
}

.background-primary-traslucent {
    background-color: hsla(210, 96%, 40%, 0.7);
    background-color: var(--primary-translucent);
}

.background-main {
    background-color: hsla(218, 23%, 21%, 1);
    background-color: var(--background);
}

.background-main-translucent {
    background-color: hsla(218, 23%, 21%, 0.85);
    background-color: var(--background-translucent);
}

.background-error {
    background-color: hsl(350, 96%, 43%);
    background-color: var(--error);
}

.background-error-translucent {
    background-color: hsla(350, 96%, 43%, 0.282);
    background-color: var(--error-translucent);
}

.foreground-accent {
    color: hsla(4, 99%, 66%, 1);
    color: var(--accent);
}

.foreground-light {
    color: hsla(182, 82%, 93%, 1);
    color: var(--light);
}

.foreground-dark {
    color: hsla(214, 35%, 37%, 1);
    color: var(--dark);
}

.foreground-primary {
    color: hsla(210, 96%, 40%, 1);
    color: var(--primary);
}

.foreground-background {
    color: hsla(218, 23%, 21%, 1);
    color: var(--background);
}

.foreground-error {
    color: hsl(350, 96%, 43%);
    color: var(--error);
}

.submit-button {
    background-color: hsla(214, 35%, 37%, 0.25);
    background-color: var(--dark-translucent);
    color: hsla(182, 82%, 93%, 1);
    color: var(--light);
    font-size: 1.25rem !important;
    text-align: center;
    border: none;
    border-radius: 10px;
    width: 50%;
    padding: 0.75rem 2rem;
    transition: all 0.7s;
    cursor: pointer;
    position: relative;
}

.submit-button:focus {
    outline: none;
}

@media (max-width: 1000px) {
    .submit-button {
        width: 85%;
    }
}

.submit-button:hover {
    box-shadow: 0 2px 15px rgba(254, 93, 82, 0.7);
    box-shadow: 0 2px 15px var(--accent-translucent);
}

.submit-border {
    border: 2.5px solid hsla(4, 99%, 66%, 1);
    border: 2.5px solid var(--accent);
    color: hsla(4, 99%, 66%, 1);
    color: var(--accent);
    background-color: transparent;
    font-size: 1.25rem !important;
    text-align: center;
    border-radius: 10rem;
    width: 50%;
    padding: 0.75rem 2rem;
    transition: all 0.7s;
    cursor: pointer;
}

.submit-border.dark {
    border: 2.5px solid hsla(218, 23%, 21%, 1);
    border: 2.5px solid var(--background);
    color: hsla(218, 23%, 21%, 1);
    color: var(--background);
}

.submit-border:hover {
    background-color: hsla(4, 99%, 66%, 1);
    background-color: var(--accent);
    color: hsla(182, 82%, 93%, 1);
    color: var(--light);
}

.submit-border.dark:hover {
    background-color: hsla(218, 23%, 21%, 1);
    background-color: var(--background);
}

.heading-font {
    font-family: 'Goldman', cursive;
    font-family: var(--heading-font);
    /* text-transform: ; */
}

.subheading-font {
