.loading {
    width: 100%;
    height: 100%;
    overflow: hidden;
    margin: 0;
    padding: 0;
    z-index: 200;
    position: absolute !important;
    top: 0;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    background-color: var(--background-translucent);
    opacity: 0;
    pointer-events: none;
    transition: all 0.5s ease-in;
}

.loading.shown {
    opacity: 1;
    pointer-events: all;
}

.loader {
    -webkit-animation: spin 1.5s linear alternate infinite;
            animation: spin 1.5s linear alternate infinite;
    background: #b73f41;
    border-radius: 50%;
    height: 120px;
    width: 120px;
}

.loader:before {
    background: #b73f41;
    border-radius: 50%;
    content: '';
    display: block;
    height: 0.5em;
    width: 0.5em;
    z-index: 2;
}

.loader:after {
    background: var(--background);
    border-radius: 50%;
    box-shadow: 0em -2.6em var(--background),
        2.25em -4.02em transparent,
        2.25em -1.25em var(--background),
        4.6em 0em transparent,
        2.25em 1.25em var(--background),
        2.25em 4.02em transparent,
        0em 2.6em var(--background),
        -2.25em 4.02em transparent,
        -2.25em 1.25em var(--background),
        -4.6em 0em transparent,
        -2.25em -1.25em var(--background),
        -2.25em -4.02em transparent;
    content: '';
    display: block;
    height: 2em;
    width: 2em;
}

.inner {
    -webkit-animation: load 1.5s linear alternate infinite;
            animation: load 1.5s linear alternate infinite;
    border: solid 1px #b73f41;
    border-radius: 50%;
    height: 1.75em;
    width: 1.75em;
    z-index: 1;
}

.loader,
.loader:before,
.loader:after,
.inner {
    bottom: 0;
    left: 0;
    margin: auto;
    position: absolute;
    right: 0;
    top: 0;
}

@-webkit-keyframes load {
    0% {
        box-shadow: 0em -2.6em transparent,
            2.25em -1.25em transparent,
            2.25em 1.25em transparent, 0em 2.6em transparent,
            -2.25em 1.25em transparent,
            -2.25em -1.25em transparent;
    }
    15% {
        box-shadow: 0em -2.6em transparent,
            2.25em -1.25em transparent,
            2.25em 1.25em transparent, 0em 2.6em transparent,
            -2.25em 1.25em transparent,
            -2.25em -1.25em #b73f41;
    }
    30% {
        box-shadow: 0em -2.6em transparent,
            2.25em -1.25em transparent,
            2.25em 1.25em transparent, 0em 2.6em transparent,
            -2.25em 1.25em #b73f41, -2.25em -1.25em #b73f41;
    }
    45% {
        box-shadow: 0em -2.6em transparent,
            2.25em -1.25em transparent,
            2.25em 1.25em transparent, 0em 2.6em #b73f41,
            -2.25em 1.25em #b73f41, -2.25em -1.25em #b73f41;
    }
    60% {
        box-shadow: 0em -2.6em transparent,
            2.25em -1.25em transparent,
            2.25em 1.25em #b73f41, 0em 2.6em #b73f41,
            -2.25em 1.25em #b73f41, -2.25em -1.25em #b73f41;
    }
    75% {
        box-shadow: 0em -2.6em transparent,
            2.25em -1.25em #b73f41, 2.25em 1.25em #b73f41,
            0em 2.6em #b73f41, -2.25em 1.25em #b73f41,
            -2.25em -1.25em #b73f41;
    }
    90% {
        box-shadow: 0em -2.6em #b73f41,
            2.25em -1.25em #b73f41, 2.25em 1.25em #b73f41,
            0em 2.6em #b73f41, -2.25em 1.25em #b73f41,
            -2.25em -1.25em #b73f41;
    }
    100% {
        box-shadow: 0em -2.6em #b73f41,
            2.25em -1.25em #b73f41, 2.25em 1.25em #b73f41,
            0em 2.6em #b73f41, -2.25em 1.25em #b73f41,
            -2.25em -1.25em #b73f41;
    }
}

@keyframes load {
    0% {
        box-shadow: 0em -2.6em transparent,
            2.25em -1.25em transparent,
            2.25em 1.25em transparent, 0em 2.6em transparent,
            -2.25em 1.25em transparent,
            -2.25em -1.25em transparent;
    }
    15% {
        box-shadow: 0em -2.6em transparent,
            2.25em -1.25em transparent,
            2.25em 1.25em transparent, 0em 2.6em transparent,
            -2.25em 1.25em transparent,
            -2.25em -1.25em #b73f41;
    }
    30% {
        box-shadow: 0em -2.6em transparent,
            2.25em -1.25em transparent,
            2.25em 1.25em transparent, 0em 2.6em transparent,
            -2.25em 1.25em #b73f41, -2.25em -1.25em #b73f41;
    }
    45% {
        box-shadow: 0em -2.6em transparent,
            2.25em -1.25em transparent,
            2.25em 1.25em transparent, 0em 2.6em #b73f41,
