    }
    60% {
        box-shadow: 0em -2.6em transparent,
            2.25em -1.25em transparent,
            2.25em 1.25em #b73f41, 0em 2.6em #b73f41,
            -2.25em 1.25em #b73f41, -2.25em -1.25em #b73f41;
